var submitBtn = document.getElementById('form-submit');

submitBtn.addEventListener('click', function (e) {
	e.preventDefault();
	var contactForm = document.getElementById('contact-form');

	var formData = getData(contactForm);

	var verifyResponse = verifyData(formData);

	document.getElementById('form-hint').textContent = verifyResponse.message;

	if (verifyResponse.valid == false) {
		document.getElementById('form-hint').classList.add('--warning');
	}
});

function getData(form) {
	if (!form || form.nodeName !== "FORM") {
		return;
	}
	var group = {};
	var i = void 0,
	    j = void 0,
	    q = [];
	for (i = form.elements.length - 1; i >= 0; i = i - 1) {
		if (form.elements[i].name === "") {
			continue;
		}
		switch (form.elements[i].nodeName) {
			case 'INPUT':
				switch (form.elements[i].type) {
					case 'text':
					case 'email':
					case 'hidden':
					case 'password':
					case 'button':
					case 'reset':
					case 'submit':
						q.push(form.elements[i].name + "=" + encodeURIComponent(form.elements[i].value));
						group[form.elements[i].name] = encodeURIComponent(form.elements[i].value);
						break;
					case 'checkbox':
					case 'radio':
					case 'file':
						break;
				}
				break;
			case 'TEXTAREA':
				group[form.elements[i].name] = encodeURIComponent(form.elements[i].value);
				break;
			case 'SELECT':
				switch (form.elements[i].type) {
					case 'select-one':
						group[form.elements[i].name] = encodeURIComponent(form.elements[i].value);
						break;
					case 'select-multiple':
						for (j = form.elements[i].options.length - 1; j >= 0; j = j - 1) {
							if (form.elements[i].options[j].selected) {
								group[form.elements[i].name] = encodeURIComponent(form.elements[i].options[j].value);
							}
						}
						break;
				}
				break;
			case 'BUTTON':
				break;
		}
	}

	return group;
}

